.container.testinomial__container {
    width: 70%; /* Default width for larger screens */
    background: rgb(24, 42, 128);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.container.testinomial__container:hover {
    border-color: var(--color-primary-varient);
    background: transparent;
}
#testinomial h3{
    text-align: center;
    color: rgb(20, 240, 233);
}
.client__avatar {
    width: 10rem;
    aspect-ratio: 1/1;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto 1rem;
    border: 0.4rem solid var(--color-primary-variant);
}

.testinomial {
    background: var(--color-bg-variant);
    text-align: center;
    padding: 2rem;
    border-radius: 2rem;
    user-select: none;
}

.client__review {
    color: var(--color-light);
    font-weight: 300;
    display: block;
    width: 80%;
    margin: 0.8rem auto 0;
}

.swiper-pagination-clickable .swiper-pagination-bullet {
    background: var(--color-primary);
}

/*========== Media Query (Medium Devices) ==========*/
@media screen and (max-width: 1024px) {
    .container.testinomial__container {
        width: 60%; /* Adjust width for tablets */
    }

    .client__review {
        width: 90%; /* Adjust width of the review text for tablets */
    }
}

/*========== Media Query (Small Devices) ==========*/
@media screen and (max-width: 600px) {
    .container.testinomial__container {
        width: 100%; /* Full width for small screens */
        padding: 1rem; /* Adjust padding for small screens */
    }

    .client__review {
        width: 100%; /* Full width for review text */
        font-size: 0.85rem; /* Adjust font size for readability on small screens */
    }

    .client__avatar {
        width: 8rem; /* Resize avatar for small screens */
    }
}
