/* Base Styles */
#about h3{
    text-align: center;
    color: rgb(35, 219, 219);
}
.about__details{

    background: transparent;
    margin-top: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
    
}
.about__details:hover{
    background: #1a52c1;
    margin-top: 10px;
    border-radius: 10px;
   
    
}
.about__details h3{
    margin-top: 10px;
    border-radius: 10px;
    text-align: center;
}


.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 10%;
    align-items: center;
    padding: 1rem;
}

.about__me {
    width: 100%;
    aspect-ratio: 1 / 1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent);
    display: grid;
    place-items: center;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: transform 0.3s ease-in-out;
}

.about__me-image:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: all 0.3s ease-in-out;
    background: rgb(9, 48, 165);
    border-color: var(--color-primary);
    color: rgb(255, 255, 255);
    cursor: pointer;
}

.about__card:hover {
    background: rgb(227, 27, 217);
    border-color: var(--color-primary);
    color: rgb(255, 255, 255);
    cursor: pointer;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 1rem;
}

.about__card small {
    font-size: 0.8rem;
    color: var(--color-light);
}

.about__content p {
    margin: 1.5rem 0;
    color: var(--color-light);
    font-size: 1rem;
    line-height: 1.5;
    text-align: justify;
}
#about h2{
    font-size: 2.5rem;
    margin-bottom: 5rem;
    text-align: center;

}
/*========== Media Query for Medium Devices ==========*/
@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 2rem;
        padding-top: 0.8rem; /* Reduced padding-top */
        margin-top: 0.5rem; /* Reduced margin-top */
    }

    .about__me {
        width: 70%;
        margin: 0 auto;
    }

    .about__content p {
        margin: 1rem 0;
    }
}

/*========== Media Query for Small Devices ==========*/
@media screen and (max-width: 600px) {
    .about__me {
        width: 80%;
        margin: 0 auto 2%;
    }

    .about__cards {
        grid-template-columns: 1fr;
        gap: 1rem;
        margin-top: 10%;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }

    .about__container {
        padding-top: 0.5rem; /* Reduced padding-top for mobile */
        margin-top: 0.2rem; /* Reduced margin-top for mobile */
    }
}

/*========== Media Query for Very Small Devices ==========*/
@media screen and (max-width: 480px) {
    .about__me {
        width: 90%;
        margin: 0 auto;
    }

    .about__cards {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .about__content p {
        font-size: 0.9rem; /* Reduced font-size for readability on smaller screens */
        margin: 1rem 0;
    }

    .about__container {
        padding-top: 0.3rem; /* Further reduced padding-top for very small screens */
        margin-top: 0.2rem; /* Further reduced margin-top for very small screens */
    }
}
