.services__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    width: 100%;
}

.service {
    background: var(--color-bg-varient);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color-primary);   
    height: fit-content;
    transition: var(--transition);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow for better aesthetics */
}
#services h3{
   text-align: center;
   color: rgb(20, 240, 233); /* Subtle shadow for better aesthetics */
}
#services h2{
    text-align: center;
    color: rgb(246, 246, 246); /* Subtle shadow for better aesthetics */
 }
 #services p{
    text-align: center;
    color: rgb(246, 246, 246); /* Subtle shadow for better aesthetics */
 }

.service:hover {
    background: transparent;
    border-color: var(--color-primary-varient);
    cursor: pointer;
}

.service__head {
    background: rgb(46, 160, 160);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);
}

.service__head h3 {
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}

.service__list {
    padding: 2rem;
}

.service__list li {
    display: flex;
    gap: 1rem;
    margin-bottom: 0.8rem;
}

.service__list-icon {
    color: var(--color-primary);
    margin-top: 2px;
}

.service__list p {
    font-size: 0.9rem;
}

/*========== Media Query (Medium Devices) ==========*/
@media screen and (max-width: 1024px) {
    .services__container {
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
    }

    .service {
        width: 100%; /* Ensure services take full width on medium screens */
        padding: 1.5rem;
    }

    .service__head h3 {
        font-size: 1rem;
    }

    .service__list {
        padding: 1rem;
    }
}

/*========== Media Query (Small Devices) ==========*/
@media screen and (max-width: 600px) {
    .services__container {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }

    .service {
        padding: 1.5rem;
    }

    .service__head h3 {
        font-size: 0.9rem; /* Adjust font size for smaller screens */
    }

    .service__list {
        padding: 1rem;
    }

    .service__list p {
        font-size: 0.85rem; /* Slightly smaller text for mobile */
    }
}
