.container.contact__container {
    width: 58%; /* Default width for large screens */
    display: grid;
    grid-template-columns: 30% 58%;
    gap: 12%;
}

.contact__options {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}
#contact h3{
    text-align: center;
    color: rgb(20, 240, 233);
}

.contact__option {
    background: var(--color-bg-varient);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.contact__option:hover {
    background: transparent;
    border-color: var(--color-primary-varient);
}

.contact__option-icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

/*============= FORM ===========*/
form {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

input, textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: rgb(179, 222, 245); /* You can specify a background color here if needed */
    border: 2px solid var(--color-primary-variant);
    resize: none;
}

/*========== Media Query (Medium Devices) ==========*/
@media screen and (max-width: 1024px) {
    .container.contact__container {
        grid-template-columns: 1fr 1fr; /* Switch to two columns layout */
        gap: 2rem; /* Adjust gap for tablets */
    }

    /* Adjust form input size and padding for medium devices */
    input, textarea {
        padding: 1.2rem;
    }
}

/*========== Media Query (Small Devices) ==========*/
@media screen and (max-width: 600px) {
    .container.contact__container {
        gap: 1rem;
        width: 100%; /* Full width for small devices */
        padding: 1rem; /* Padding adjustment for smaller screens */
    }

    /* Adjust form and container widths */
    .contact__options {
        gap: 1rem; /* Adjust the gap for small screens */
    }

    .contact__option {
        padding: 1rem; /* Reduced padding for small screens */
    }

    input, textarea {
        padding: 1rem; /* Adjust input padding for small devices */
    }

    .contact__option a {
        font-size: 0.75rem; /* Reduce font size for small screens */
    }
}
