.portfolio__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* Default for desktop */
    gap: 2.5rem;
}

.portfolio__item {
    background: var(--color-bg-varient);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}
#portfolio h3{
    text-align: center;
    color: rgb(20, 240, 233);
}
.portfolio__item:hover {
    border-color: var(--color-primary-varient);
    background: transparent;
}

.portfolio__item-image {
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3 {
    margin: 1.2rem 0 2rem;
    text-align: center;
}

.portfolio__item-cta {
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
    justify-content: center; /* Center the cta buttons/icons */
}
#portfolio h3 {
    text-align: center;
    justify-content: center;
    color: aqua; /* Center the cta buttons/icons */
}

/*========== Media Query (Medium Devices) ==========*/
@media screen and (max-width: 1024px) {
    .portfolio__container {
        grid-template-columns: 1fr 1fr; /* 2 columns for tablets */
        gap: 1.2rem;
    }

    .portfolio__item {
        padding: 1rem; /* Reduce padding for medium screens */
    }

    .portfolio__item h3 {
        font-size: 1.1rem; /* Adjust font size */
    }
}

/*========== Media Query (Small Devices) ==========*/
@media screen and (max-width: 600px) {
    .portfolio__container {
        grid-template-columns: 1fr; /* 1 column for mobile screens */
        gap: 1rem;
    }

    .portfolio__item {
        padding: 1rem; /* Reduce padding for small screens */
    }

    .portfolio__item h3 {
        font-size: 1rem; /* Adjust font size for small screens */
    }

    .portfolio__item-cta {
        flex-direction: column; /* Stack the cta buttons/icons for smaller devices */
    }
}
