/*========== General Styles ==========*/
header {
    height: 125vh;
    padding-top: 5rem;
    overflow: hidden;
    position: relative;
}

.text-black{
    color: #f5f5f5;
    background: #e06c19;
    border-radius: 10px;
}
.text-black:hover{
    color: #f5f5f5;
    background: #1962e0;
    border-radius: 10px;
}
.text-black1{
    margin-top: 10px;
    color: #fbfbfb;
    background: #079ec0;
    border-radius: 10px;
}
.text-black1:hover{
    margin-top: 10px;
    color: #fbfbfb;
    background: #c05407;
    border-radius: 10px;
}
.header__container {
    text-align: center;
    height: 100%;
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* gap: 0rem; Space between elements */
    padding: 0 1.5rem; /* Padding for smaller devices */
}

/*======== CTA (Call to Action) =========*/
.cta {
    display: flex;
    gap: 1.2rem;
    justify-content: center;
    margin-top: 1rem;
    flex-wrap: wrap; /* Adjust buttons for smaller screens */
}

/*======== Header Social =========*/
.header__social {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 1rem;
    bottom: 3rem;
}

/*======== ME (Image) =========*/
.me {
    width: 50%;
    max-width: 25rem;
    height: auto;
    position: relative;
    margin-top: 2rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    background: linear-gradient(var(--color-primary), transparent);
    display: flex;
    align-items: center;
    justify-content: center;
}

.me img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 12rem 12rem 0 0;
}

/*======== Scroll Down =========*/
.scroll__down {
    position: absolute;
    right: 1rem;
    bottom: 3rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
    white-space: nowrap;
    color: var(--color-primary);
}

/*========== Media Queries ==========*/

/* Medium Devices (Tablets) */
@media screen and (max-width: 1024px) {
    header {
        height: 130vh;
    }

    .me {
        width: 35%;
        max-width: 20rem;
        height: auto;
    }

    .scroll__down {
        right: 0.8rem;
        bottom: 2rem;
    }

    .header__social {
        left: 1rem;
        bottom: 2rem;
    }
}

/* Small Devices (Phones) */
@media screen and (max-width: 768px) {
    header {
        height: 90vh;
    }

    .cta {
        flex-direction: column;
        gap: 1rem;
    }

    .me {
        width: 50%;
        max-width: 18rem;
        height: auto;
    }

    .scroll__down {
        display: none; /* Hide for smaller screens */
    }

    .header__social {
        display: none; /* Optional: Hide social icons for smaller screens */
    }
}

/* Extra Small Devices (Phones) */
@media screen and (max-width: 480px) {
    header {
        height: auto;
        padding-top: 4rem;
    }

    .header__container {
        gap: 1rem;
        padding: 0 1rem;
    }

    .me {
        width: 70%;
        max-width: 15rem;
        height: auto;
    }

    h2 {
        font-size: 1.2rem;
    }

    h1 {
        font-size: 1.5rem;
    }

    .cta {
        gap: 0.8rem;
    }
}
