.experience__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  width: 100%;
}

.experience__container > div {
  background: var(--color-bg-varient);
  padding: 2.4rem 4rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow for better aesthetics */
}

.experience__container > div:hover {
  background: transparent;
  border-color: var(--color-primary);
  cursor: pointer; /* Feedback to indicate interaction */
}

.experience__container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--color-primary);
}

.experience__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 2rem;
  gap: 1.5rem; /* Added column spacing for better alignment */
}
#experience h3{
  text-align: center;
  color: rgb(20, 240, 233);
}
.experience__details {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.experience__details-icon {
  margin-top: 4px;
  color: var(--color-primary);
  font-size: 1.5rem; /* Adjust size for consistency */
}

/*========== Media Query (Medium Devices) ==========*/
@media screen and (max-width: 1024px) {
  .experience__container {
      grid-template-columns: 1fr;
      gap: 1.5rem;
  }

  .experience__container > div {
      width: 90%;
      padding: 2rem;
      margin: 0 auto;
  }

  .experience__content {
      grid-template-columns: 1fr; /* Stack content for smaller screens */
      padding: 1rem;
  }
}

/*========== Media Query (Small Devices) ==========*/
@media screen and (max-width: 600px) {
  .experience__container {
      gap: 1rem;
  }

  .experience__container > div {
      width: 100%;
      padding: 1.5rem 1rem;
  }

  .experience__content {
      grid-template-columns: 1fr; /* Stack items on smaller devices */
      row-gap: 1.5rem;
  }

  .experience__details-icon {
      font-size: 1.2rem; /* Slightly smaller icons for mobile */
  }
}
